import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { API_URL } from '../constants/constants';
import { appConfig } from "./../services/config.service";
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import { EMPTY, of } from "rxjs";
import { CommonService } from './common.service';


@Injectable({
  providedIn: 'root'
})

export class LoginService {

  constructor(private http: HttpClient, private router: Router, private CommonService: CommonService) { }

  private headers = new HttpHeaders({ 'Content-Type': 'application/json' });

  login(user: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/Login/LoginUser', user, { headers: this.headers }).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  refreshToken(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/Login/RefreshToken', data, { headers: this.headers }).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  registerClient(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/DialService/RegisterClient', data, { headers: this.headers }).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  registerSupervisor(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/DialService/RegisterSupervisor', data, { headers: this.headers }).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  getSecretQuestion(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/Login/GetSecretQuestion', data, { headers: this.headers }).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  verifySecretAnswer(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/Login/verifySecretAnswer', data, { headers: this.headers }).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  logout(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/Login/LogoutUser', data, { headers: this.headers }).pipe(
      retry(1),
      catchError(this.handleError)
    )
  }

  redirect() {
    this.CommonService.clearStorage();
    this.router.navigate(['/']);
  }
  
  gotoNotFound() {
    this.router.navigate(['/403']);
  }

  handleError(error: any) {
    return throwError(error.message || error);
  }

}
